.navbar{
    font-family: 'Poppins', sans-serif;
    background-color: white;
    width: 100%;
    margin:0;
    position: fixed;
    border-bottom: 1px solid #ccc;
    z-index: 1000;
}

.navbar-toggler{
    border-color: #333333;
}
.navbar-toggler-icon{
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(51,51,51, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E")
}

.navbar-brand{
    margin: right;
    font-weight: 600;
}

.navbar-brand > .company-name{
    color: #fe4701;
}

.navbar-brand > .domain-name{
    color: #333333;
}

.nav-items{
    justify-content: center;
}

.nav-button{
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    color: #bbafaf;
    position: relative;
    margin: auto;
    /* padding: 6px 21px 6px 21px; */
}
.nav-button:hover{
    text-decoration: none !important;
    color:#333333;
    transition: 0.5s;
    border-radius: 0;
    border-top: 1px solid #333333;
    border-bottom: 1px solid #333333;
}

.login {
    --color: #fe4701;
    --hover: #ef8f6e;
    --hovbg: #fe4701;
    --hovcolor: #ffffff;
    /* --color: #8fc866;
    --hover: #66c887; */
}

.signup{
    background-color: #fe4701;
    --color: #ffffff;
    --hover: #ef8f6e;
    --hovbg: #ffffff;
    --hovcolor: #fe4701;
}

.raise {
    margin-left: 10px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    color: #bbafaf;
    border-radius: 3px;
    border: 1px solid ;
    color: var(--color);
    transition: 0.25s;
}

.raise:hover, .raise:focus {
    background-color: var(--hovbg);
    border-color: var(--hover);
    color: var(--hovcolor);
    box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
    transform: translateY(-0.25em);
}

