.blink{
    border-bottom: 1px solid;
    -webkit-animation: underBlink 1s infinite;
            animation: underBlink 1s infinite;
}

@-webkit-keyframes underBlink {
    0%   { border-bottom: 5px solid #ffd3c1; }
    100% { border-bottom: 5px solid #fe4701; }
}

@keyframes underBlink {
    0%   { border-bottom: 5px solid #ffd3c1; }
    100% { border-bottom: 5px solid #fe4701; }
}

.center{
    width: 100%;
    margin-top: 8%;
    justify-content: center;
    text-align: center;
}

.parseBtn{
    font-size: 20px;
    margin-top: 10px;
    text-align: center;
    background: #fff;
    border: 1px solid #fe4701;
    border-radius: 3px;
    box-shadow: 3px 3px 5px #fe4701;
}

.btnBlink{
    -webkit-animation: btnBlink 1s infinite;
            animation: btnBlink 1s infinite;
}

.parseBtn .icon{
    color: #fff;
    display: none;
}

@-webkit-keyframes btnBlink {
    0%   { box-shadow: none;}
    100% { border: 1px solid #fe4701; }
}

@keyframes btnBlink {
    0%   { box-shadow: none;}
    100% { border: 1px solid #fe4701; }
}

.parseBtn:hover {
    border: 1px solid #fe4701;
    /* box-shadow: none; */
    box-shadow: 3px 3px 5px #fe4701;
    /* background-color: #fe4701; 
    color: #fff; */
    -webkit-animation: btnBlinkStop 1s infinite;
            animation: btnBlinkStop 1s infinite;
    transition: 500ms;
}

.parseBtn:hover .icon{
    padding-left: 10px;
    display: inline-flex;
    color: #000;
    transition: 1s;
}

@-webkit-keyframes btnBlinkStop{
    100% { border: 1px solid #fe4701; }
}

@keyframes btnBlinkStop{
    100% { border: 1px solid #fe4701; }
}

